/* 공통 선언 */

/* layout */
.front_body{}
.front_body.flex_body{}
.page_wrap{min-width:280px;overflow:hidden;position:relative;}
.page_wrap.flex_wrap{}
.aside_menu_respon_zone{position:fixed;top:0;left:0;width:250px;height:100%;z-index:40}
.aside_menu_zone{display:flex;overflow-y:auto;position:fixed;top:0;left:0;width:250px;height:100%;background:#162b3f;flex-direction: column;justify-content: space-between;}
.win .aside_menu_zone::-webkit-scrollbar {width: 10px;}
.win .aside_menu_zone::-webkit-scrollbar-thumb {background-color: rgba(255,255,255,.5);border-radius: 10px;background-clip: padding-box;border: 2px solid transparent;opacity:.5;}
.win .aside_menu_zone::-webkit-scrollbar-track {background-color: transparent;border-radius: 10px;}


.right_content_zone{background:#edeef3;min-height:100vh;position:relative;}
.right_content_zone.type2{background:#fff;}
.right_content_zone.type3{min-height:0;}
.aside_dim{position:fixed;top:0;left:0;width:100%;height:100%;background:#000;opacity:0;transition: .5s;display:none;}

.visual_header{position:relative;}
.head_toplogo{display:block;width:189px;height:32px;background:url("../images/login_logo.png") no-repeat 0 0/100% auto;position:absolute;top:22px;left:33px;}

.visual_fxwrap{display:flex;align-items: center;justify-content: center;text-align: center;height:240px;background:url("../images/bg_sub.jpg") no-repeat 50% 50%/cover;}
.visual_fxwrap.bgtype2{background-image:url("../images/bg_sub2.jpg")}
.visual_title{font-size:34px;color:#fff;letter-spacing: -1px;font-weight:500;word-break: keep-all;}
.mid_section{background:#edeef3;min-height:calc(100vh - 296px)}
.mid_titlelow{padding:50px 30px 30px;text-align: center;}
.mid_title{font-size:28px;color:#1b1b1b;font-weight:500;line-height:1.3;letter-spacing: -1px;}
.mid_content_box{margin:0 auto;max-width:1000px;padding:0 30px 30px;}
.mid_content_box.type2{max-width:800px;}
.mid_content{background:#fff;border:1px solid #e2e2e2;border-radius:10px;padding:40px 40px 55px;}
.mid_content_in{max-width:620px;margin:0 auto;}
.mid_content_in.type2{max-width:100%;}
.mid_content_in.type2 .mid_content{padding:50px;}
.wfooter_zone{}
.wfooter_zone .wfooter_text{}
.wfooter_zone .wfooter_copy{}
.wfooter_text,
.wfooter_copy{font-size:12px;color:#999999;display:inline-block;}
.wfooter_text{}
.wfooter_copy{margin-left:12px;}
.wfooter_zone{height:56px;background:#fff;width:100%;display:flex;align-items: center;justify-content: center;box-sizing: border-box;border-top:1px solid #e4e4e4;}
.mid_content{font-size:12px;}

.page_wrap.type2 .mid_section{background:#ffffff;}
.page_wrap.type2 .wfooter_zone{background:#f9f9f9;border-top-color:#e4e4e4;}

.mid_wbox_wrap{max-width:830px;margin:0 auto;padding:0 30px 265px;}

// mobile
.mb_header{display:none;position:fixed;top:0;left:0;width:100%;height:60px;background:#162b3f;z-index:30}
.btn_totalcall{position:absolute;top:0;left:0;width:60px;height:60px;display:flex;align-items: center;justify-content: center;}

.btn_htotal{display:flex;align-items: center;justify-content: center;width:60px;height:100%;}
.total_in{display:block;height:2px;background:#fff;width:1.3125rem;position:relative;}
.total_in::before,
.total_in::after{display:block;content:'';width:100%;height:2px;background:#fff;position:absolute;top:0;left:0;}
.total_in::before{transform: translateY(-0.4375rem);}
.total_in::after{transform: translateY(0.4375rem);width:88.0952380952381%;}

.total_in{}
.total_in:before{}
.total_in:after{}
.mbtoplogo{display:flex;align-items: center;justify-content: center;height:100%;}
.mbtoplogo_link{display:inline-block;}
.mbtoplogo_link img{height:30px;width:auto;vertical-align: top;}
.btn_mypagego{position:absolute;top:0;right:0;width:60px;height:60px;display:flex;align-items: center;justify-content: center;}
.mypagego_thum{display:inline-block;width:28px;height:28px;border:1px solid #d7d7d7;border-radius:100%;background:#fff url("../images/ico_util_man.png") no-repeat 50% 50%/cover}

.aside_top{}
.aslogo_low{}
.astoplogo{height:60px;display:flex;align-items: center;justify-content: center;background:#24374a;}
.astoplogo_link{display:inline-block;}
.astoplogo_link img{vertical-align: top;width:171px;}
.asmenu_low{}
.asmenu_vlist{position:relative;overflow: hidden;}
.asmenu_vlist > li{border-bottom:1px solid #24374a;}
.asmenu_vlist > li:last-of-type{}
.asmenu{min-height:72px;position:relative;display:flex;align-items: center;box-sizing: border-box;padding:15px 20px;transition: .4s;}
.asmenu:after{display:block;content:'';transition:.4s;width:1px;height:0;position:absolute;top:-1px;left:0;background:#34b371;}
.asmenu_vlist > li.active .asmenu,
.asmenu:hover{background:#122232;}
.asmenu_vlist > li.active .asmenu:after,
.asmenu:hover:after{height:calc(100% + 2px);}
.asmenu:before{flex:0 0 35px;display:block;content:'';width:35px;height:35px;background:url("../images/ico_lm.png") no-repeat 0 0/100% auto;}
.asmenu.ico_home{}
.asmenu.ico_home:before{background-position:0 0;}
.asmenu.ico_main{}
.asmenu.ico_main:before{background-position:0 -35px;}
.asmenu.ico_sub{}
.asmenu.ico_sub:before{background-position:0 -70px;}
.asmenu.ico_history{}
.asmenu.ico_history:before{background-position:0 -105px;}
.asmenu_text{display:inline-block;color:#fff;font-size:16px;margin-left:7px;}

.asmenu_vlist.type2 .asmenu:before{flex:0 0 24px;width:24px;height:24px;background:url("../images/ico_lm2.png") no-repeat 0 0/100% auto;}
.asmenu_vlist.type2 .asmenu.ico_reserve:before{background-position:0 0;}
.asmenu_vlist.type2 .asmenu.ico_fly_history:before{background-position:0 -24px;}
.aside_bottom{}
.icobox_link_w{padding:15px;margin-bottom:15px;}
.icobox_link{display:flex;align-items:center;margin-bottom:10px;border-radius:7px;background:#1c3a57;padding:15px 45px 15px 15px;position:relative;}
.icobox_link:last-of-type{margin-bottom:0}
.icobox_linktext{font-size:15px;color:#dddddd;line-height:1.4;margin-left:10px;word-break: keep-all;}
.icobox_link:before{flex:0 0 33px;display:block;content:'';width:33px;height:33px;border-radius:100%;background:#314c67 url("../images/ico_men_cir.png") no-repeat 50% 50%/20px auto;}
.icobox_link:after{
    display:block;content:'';position:absolute;top:50%;right:22px;margin-top:-5px;width:10px;height:10px;border-left:1px solid #fff;border-bottom:1px solid #fff;
    transform: translateZ(0);-webkit-transform: translateZ(0);-ms-transform: translateZ(0);-moz-transform: translateZ(0);
    transform:rotate(225deg);-ms-transform:rotate(225deg);-webkit-transform:rotate(225deg);opacity:.7;
}
.icobox_link:last-of-type{}

.aside_emenuitem_w{padding:0 30px;}
.aside_emenu_title{font-size:14px;color:#b5b5b5;line-height:1;margin-bottom:20px;}
.aside_emenu_vlist_w{}
.aside_emenu_vlist{margin:0 -2px;}
.aside_emenu_vlist > li{margin-bottom:15px;}
.aside_emenu_vlist > li:last-of-type{margin-bottom:0;}
.aside_emenu{display:flex;align-items: center;}
.aside_emenu:before{flex:0 0 26px;position:relative;top:1px;display:block;content:'';width:26px;height:26px;background:url("../images/ico_lmelse.png") no-repeat 0 0/100% auto;opacity:.32;}
.aside_emenu:hover{}
.aside_emenu.ico_profile{}
.aside_emenu.ico_profile:before{background-position:0 0;}
.aside_emenu.ico_help{}
.aside_emenu.ico_help:before{background-position:0 -26px;}
.aside_emenu.ico_logout{}
.aside_emenu.ico_logout:before{background-position:0 -52px;}
.emenu_text{flex:1;display:inline-block;font-size:14px;color:#e8e8e8;line-height:1;margin-left:10px;}
.aside_footer{text-align:center;padding:18px 0;border-top:1px solid #2c3f51;margin-top:30px;}
.asfoot_company,
.asfoot_copy{display:block;}
.asfoot_company{font-size:13px;color:#dddddd;}
.asfoot_copy{font-size:11px;color:#909090;margin-top:5px;}

.right_content_zone{margin-left:250px;}
.right_mapfxwrap{display:flex;justify-content: space-between;align-items: center;min-height:37px;max-width:1200px}
.right_maplow{box-sizing: border-box;background:#fff;padding:10px 30px;border-bottom:1px solid #dcdcdc;}
.map_cell{}
.submap_hlist{display:flex;align-items: center;font-size:0;line-height:0;}
.submap_hlist > li{position:relative;padding-right:23px;}
.submap_hlist > li:after{
   display:block;content:'';
    border-right:1px solid #a4a4a4;border-bottom:1px solid #a4a4a4;width:7px;height:7px;position:absolute;top:3px;right:10px;
    transform: translateZ(0);-webkit-transform: translateZ(0);-ms-transform: translateZ(0);-moz-transform: translateZ(0);
    -webkit-transform:rotate(-45deg);transform:rotate(-45deg);-moz-transform:rotate(-45deg);-ms-transform:rotate(-45deg);
}
.submap_hlist > li:last-of-type{padding-right:0;}
.submap_hlist > li:last-of-type:after{display:none;}
.submap_menu{display:inline-block;font-size:14px;color:#333333;}
.submap_text{}
.submap_home{display:flex;align-items: center;}
.submap_home .submap_text{font-size:14px;font-weight:350;color:#4e4e4e;display:inline-block;line-height:1;}
.submap_home:before{flex:0 0 12px;display:block;content:'';width:12px;height:12px;margin-right:8px;background:url("../images/ico_maphome.png") no-repeat 0 0/100% auto;}
.submap_text{display:inline-block;line-height:1;}
.util_cell{}
.util_hlist{display:flex;align-items: center;}
.util_date_input_w{position:relative;}
.util_date_input_w:after{
    display:block;content:'';position:absolute;top:-2px;right:4px;width:10px;height:10px;border-right:1px solid #727272;border-bottom:1px solid #727272;
    transform: translateZ(0);-webkit-transform: translateZ(0);-ms-transform: translateZ(0);-moz-transform: translateZ(0);
    transform:rotate(45deg);-ms-transform:rotate(45deg);-webkit-transform:rotate(45deg);
}
input.util_range{border:0;font-size:14px;line-height:1;height:14px;width:200px;padding:0;padding-right:27px;text-align: right;}
.btn_utilmen{display:inline-block;width:28px;height:28px;border:1px solid #d7d7d7;border-radius:100%;background:url("../images/ico_util_man.png") no-repeat 50% 50%/cover}

.util_hlist > *{margin-right:25px;}
.util_hlist > *:last-child{margin-right:0}

.right_contents{}

.content_intitlow{margin:0 30px;max-width:1200px;display:flex;justify-content: space-between; align-items: center;min-height:95px;border-bottom:1px solid #e7e7e7;}
.content_intit{font-size:22px;color:#1b1b1b;line-height:1;font-weight:500;letter-spacing: -.5px;}
.content_incontlow{margin:35px 30px 90px;max-width:1200px;}
.intit_else_w{display:flex;align-items: center;}
.intit_else_w .btn_cboxobj{margin-right:5px;}
.intit_else_w .btn_cboxobj:last-of-type{margin-right:0;}
.btn_cboxobj{display:inline-block;font-size:15px;border:1px solid #404d61;border-radius:100px;line-height:1;box-sizing: border-box;min-width:100px;padding:12px 24px;text-align: center;}
.btn_cboxobj.type2{background:#404d61;color:#fff;}
.intit_else_w{}
.insearch_field{flex:1;display:flex;max-width:470px;align-items: center;}
.insearch_field > input.insearch_input{flex:1;}
.insearch_field > .btn_cboxobj{margin-left:10px;}
input.insearch_input{border:1px solid #cccccc;line-height:1;border-radius:4px;height:50px;padding:0 20px;width:100%;font-size:14px;color:#000;}
.incont_title{font-size:20px;color:#1b1b1b;font-weight:500;}
@media(max-width:1023px){
    .head_toplogo{/*width:99.5px;height:16px;*/top:10px;left:12.5px;}
    .visual_fxwrap{height:160px;}
    .visual_title{font-size:22px;}
    .mid_titlelow{padding:25px 15px 15px;}
    .mid_title{font-size:18px;}
    .mid_content_box{padding:0 20px 20px;}
    .mid_content{padding:20px 20px 40px !important;}
    .wfooter_zone{height:40px;}
    .wfooter_text,.wfooter_copy{font-size:11px;}
    .mid_section{min-height:calc(100vh - 160px)}
    .mid_wbox_wrap{max-width:830px;margin:0 auto;padding:0 20px 80px;}

    .aside_menu_respon_zone{display:none;}
    .aside_menu_zone{transition: .5s;width:250px;left:-250px;}
    .aside_menu_respon_zone.active{display:block;}
    .aside_menu_respon_zone.active .aside_dim{display:block;}
    .aside_menu_respon_zone.active.motion .aside_dim{opacity: .8;}
    .aside_menu_respon_zone.active.motion .aside_menu_zone{left:0;}
    .right_content_zone{margin:0;padding-top:60px;}
    .mb_header{display:block;}

    .right_maplow{padding:10px 20px;}
    .util_hlist>*{margin-right:10px;}
    .btn_utilmen{display:none;}
    .util_date_input_w{margin-right:0 !important;}

    .content_intitlow{display:block;margin:0 20px;min-height:0;text-align: center;padding:20px 0;}
    .content_intitlow.type2{display:flex;}
    .content_intitlow.type2 .intit_else_w{margin-top:0;}
    .content_incontlow{margin:25px 20px 50px;}
    .content_intit{font-size:18px;}
    .intit_else_w{display:flex;justify-content: center;margin-top:10px;flex-wrap:wrap;}
    .btn_cboxobj{font-size:14px;min-width:60px;padding:10px 15px;text-align: center;}
    .insearch_field{margin-top:10px;}
    input.insearch_input{height:40px;padding:0 15px;}
    .insearch_field >.btn_cboxobj{margin-left:5px;}
}