@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 400;
  src: local('Noto Sans KR'),
      url('./webfont/NotoSans-Regular.woff2') format("woff2"),
      url('./webfont/NotoSans-Regular.ttf') format("truetype");
}
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 500;
  src: local('Noto Sans KR'),
      url('./webfont/NotoSans-Medium.woff2') format("woff2"),
      url('./webfont/NotoSans-Medium.ttf') format("truetype");
}
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 700;
  src: local('Noto Sans KR'), 
      url('./webfont/NotoSans-Bold.woff2') format("woff2"),
      url('./webfont/NotoSans-Bold.ttf') format("truetype");
}
/* NanumBarunGothic 기본 폰트 */
@font-face{
font-family: "NanumBarunGothic";
src: url('./webfont/NanumBarunGothic.eot')format('eot');
src: url('./webfont/NanumBarunGothic.woff') format('woff');
}
@font-face{
font-family: "NanumBarunGothicBold";
src: url('./webfont/NanumBarunGothicBold.eot')format('eot');
src: url('./webfont/NanumBarunGothicBold.woff') format('woff');
}