// main

.mc_contents_wrap{max-width:1200px;padding:15px 30px 65px 30px;}
.mc_cont_vitem{margin-bottom:35px;}
.mc_cont_vitem:last-of-type{margin-bottom:0;}
.mc_cont_vtitlow{display:flex;justify-content: space-between;align-items: center;margin-bottom:10px}
.mc_cont_vtit{font-size:19px;color:#1b1b1b;line-height:1;letter-spacing: -1px;font-weight:500;display:flex;align-items: center;min-height:40px;line-height:1;}
.mc_cont_vtitelse{}
.mc_cont_vtitelse .formselect_sm{}
.select_calendar_w{display:inline-block;background:#ffffff;border:1px solid #cccccc;border-radius: 100px;position:relative;}
.select_calendar_w:before{
    display:block;content:'';width:9px;height:9px;position:absolute;top:10px;right:19px;border-right:1px solid #6b6b6b;border-bottom:1px solid #6b6b6b;
	transform: translateZ(0);-webkit-transform: translateZ(0);-ms-transform: translateZ(0);-moz-transform: translateZ(0);
	-webkit-transform:rotate(45deg);transform:rotate(45deg);-moz-transform:rotate(45deg);-ms-transform:rotate(45deg);
}
.select_calendar{display:inline-block;background:#fff;border:0;background:transparent;height:38px;line-height:1;font-size:14px;padding:0 45px 0 46px;background:url("../images/ico_front_calendar.png") no-repeat 20px 50%/18px auto;line-height:38px;cursor: pointer;}
.mc_cont_vcontlow{}
.mv_both{display:flex;}
.mv_short,
.mv_long{flex:1;border:1px solid #e2e2e2;border-radius:8px;position:relative;overflow: hidden;background:#fff;display:flex;align-items: center;
    -webkit-box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05); 
    box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05);
}
.mv_long > img{vertical-align: top;}
.mv_short{flex:0 0 224px;margin-left:14px;}
.btn_mvlk_vlist_w{padding:15px;box-sizing: border-box;width:100%;}
.btn_mvlk_vlist{}
.btn_mvlk_vlist > li{margin-bottom:10px;}
.btn_mvlk_vlist > li:last-of-type{margin-bottom:0;}
.btn_mvlk{background:#fff;color:#333333;border:1px solid #d4d4d4;border-radius:5px;display:block;text-align: center;padding:14px;font-size:15px;line-height:1;}
.btn_mvlk.active{background-color:#344351;color:#ffffff;border-color:#344351;}
.btn_mvlk.more_type{font-size:0;line-height:0;}
.btn_mvlk.more_type:after{display:block;content:'';width:19px;height:19px;background:url("../images/ico_mplus.png") no-repeat 0 0/100% auto;margin:0 auto;}
.mc_fly_both{display:flex;margin:0 -10px;}
.mc_fly_chart{flex:1;margin:0 10px;}
.mc_fly_fbox_w{display:flex;margin:-5px;flex-wrap:wrap;}
.mc_fly_fbox_cell{flex:0 0 50%;padding:5px;box-sizing: border-box;}
.fly_fbox{
    border:1px solid #e2e2e2;border-radius:8px;position:relative;overflow: hidden;background:#fff; display:flex; align-items: flex-end;
    -webkit-box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05); 
    box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05);
    box-sizing: border-box;width:100%;height:100%;
}
.fly_fbox .data_w{position:absolute;top:0;left:0;width:100%;box-sizing: border-box;padding:20px;}
.fly_fbox .data_w.type2{position:static;width:auto;padding:20px;width:100%;box-sizing: border-box;}
.fly_fbox .mc_chartdata{display:flex;align-items: center;}
.fly_fbox .mc_chartdata_thum{flex:0 0 40px;display:block;width:40px;height:40px;background:#13d5aa url("../images/ico_mc.png") no-repeat 0 0/100% auto;border-radius:100%;}
.fly_fbox .mc_chartdata_spec{margin:0 0 0 10px;color:#333333;font-size:18px;line-height:1;letter-spacing: -.5px;flex:1;font-weight:500;}
.fly_fbox .chart_w{width:100%;}
.fly_fbox .chart_w > img{vertical-align: top;width:100%;}
.fly_fbox.type2{align-items: flex-start;}
.mc_chart_count_fxwrap{margin:0 0 0 50px;display:flex;align-items: flex-end;}
.mc_chart_count{font-size:34px;color:#333333;line-height:1;display:inline-block;font-weight:500;letter-spacing: -1px;}
.mc_chart_count_else{font-size:17px;color:#333333;line-height:1;display:inline-block;padding:0 5px 2px 5px;}

.mc_chartdata.type2 .mc_chartdata_thum{background-position:0 -40px;background-color:#8373ff;}
.mc_chartdata.type3 .mc_chartdata_thum{background-position:0 -80px;background-color:#3bcbf5;}
.mc_chartdata.type4 .mc_chartdata_thum{background-position:0 -120px;background-color:#f86185;}

.mc_contents_wrap .dotguide_w{display:flex;justify-content: flex-end;}
.mc_contents_wrap .dotguide_w .dotguide_hlist{width:calc(50% - 10px);}
.mc_contents_wrap .dotguide_w .dotcirle{top:-1px;}
.mc_contents_wrap .dotguide_w .dotguide_hlist{}

.mc_fly_map{flex:1;border:1px solid #e2e2e2;border-radius:8px;position:relative;overflow: hidden;background:#fff;
    -webkit-box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05); 
    box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05);}
.mc_map_api_zone{position: relative;width:100%;height:100%;display:flex;justify-content: center;align-items: center;}

.mc_wavechart_box{
    border:1px solid #e2e2e2;border-radius:8px;position:relative;overflow: hidden;background:#fff;
    -webkit-box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05); 
    box-shadow: 3px 3px 20px 2px rgba(0,0,0,0.05);
    padding:30px 40px 54px;
}
.mc_wavechart_box > img{vertical-align: top;}

.mc_barchart_zone{}
.mc_barchart_fxwrap{display:flex;margin:-5px;flex-wrap:wrap;}
.mc_barchart_fxcell{flex:0 0 33.3333333333333333333%;box-sizing: border-box;padding:5px;min-width:0}
.mc_barchart_box{border:1px solid #e2e2e2;border-radius:8px;box-sizing: border-box;padding:25px 23px;position:relative;overflow: hidden;background:#fff;width:100%;height:100%;}
.mc_wbox_tit{font-size:18px;color:#1b1b1b;letter-spacing: -.5px;line-height:1;position:relative;font-weight:500;padding-bottom:15px;border-bottom:1px solid #cacaca;}
.mc_wbox_tit.has_ico{padding-left:24px;}
.mc_wbox_tit.has_ico:before{display:block;content:'';width:16px;height:22px;background:url("../images/ico_mic.png") no-repeat 0 0/100% auto;position:absolute;top:0;left:0;}
.mc_wchart_zone{}
.mc_barchart_tb{display:table;width:100%;border-bottom:1px solid #e7e7e7;padding:20px 10px 16px;box-sizing: border-box;}
.mc_barchart_tr{display:table-row;}
.mc_barchart_cell{display:table-cell;vertical-align: middle;padding-bottom:16px;}
.mc_barchart_cell.define_lab{width:1px;padding-right:15px;}
.mc_barchart_cell.define_chart{}
.mc_barchart_tr:last-of-type .mc_barchart_cell{padding-bottom:0;}
.barchart_wpsp{font-size:13px;color:#6d6c6c;display:inline-block;line-height:1;white-space: nowrap;}
.barchart_w{background:#ededed;height:8px;position:relative;overflow:hidden;border-radius:100px;}
.barchart_cur{display:block;position:absolute;top:0;left:0;height:8px;border-radius:100px;}
.mc_total_tb_w{margin-top:19px;}
.mc_total_tb{display:table;width:100%;table-layout: fixed;text-align: center;line-height:1;}
.mc_total_tb th,
.mc_total_tb td{}
.mc_total_tb th{font-size:13px;color:#5f5f5f;padding:0 0 10px 0;border-bottom:1px solid #e7e7e7;}
.mc_total_tb td{font-size:16px;color:#333333;padding:10px 0 0 0;}

.mc_chart_else_data{margin:35px 0 20px 50px;display:flex;align-items: center;}
.mc_chart_else_lab{font-size:14px;}
.mc_chart_else_value{margin-left:5px;}
.mc_else_num{font-size:18px;color:#4e4e4e;font-weight:500;}
.mc_else_unit{font-size:14px;color:#4e4e4e;}

.hor_fxwrap_wrap{display:flex;border-top:1px solid #e7e7e7;padding:25px 0;border-bottom:1px solid #e7e7e7;}
.hor_fxitem{flex:1;text-align: center;}
.hor_fxlab{font-size:15px;color:#333333;line-height:1;margin-bottom:15px;font-weight:500;letter-spacing: -.4px;}
.hor_fxdata{display:flex;align-items: center;justify-content: center;align-items: center;}
.hor_fxdata.has_img{}
.hor_fxdata.has_img > img{}
.hor_fxdata{}
.hor_fxnum{font-size:22px;color:#333;letter-spacing: -1px;}
.hor_fxunit{font-size:16px;color:#333333;margin-left:3px;position:relative;top:2px;}

.line_chart_tit{font-size:16px;color:#1b1b1b;padding: 0 0 15px 0;border-bottom:1px solid #e7e7e7;text-align: center;font-weight:500;margin-top:25px}

.mc_textlk_zone{position:relative;display:flex;}
.mc_textlk_zone .nodata_para{display:flex;height:100%;width:100%;align-items: center;justify-content: center;}
.mctextlk_vlist{min-width:0;}
.mctextlk_vlist > li{border-bottom:1px solid #e7e7e7;padding:18px 0;}
.mctextlk_vlist > li:last-of-type{}
.mctextlk{display:flex;align-items: center;min-width:0}
.mctextlk_ico{flex:0 0 17px;background:url("../images/ico_info_imp.png") no-repeat 0 0/100% auto;display:inline-block;width:17px;height:17px;}
.mctextlk_text{flex:1;font-size:15px;color:#333333;display:inline-block;margin-left:5px;white-space: nowrap; text-overflow: ellipsis;overflow:hidden;position:relative; max-width:99%;min-width:0;}
.mctextlk_text .csym{color:#ed4444;}
.mctextlk_guide{margin:5px 0 0 22px;font-size:13px;color:#999999;letter-spacing: -.5px;}
.mc_textlk_zone .nodata_para{color:#999999;font-size:14px;line-height:1.4;}
@media (max-width:1280px){
    .fly_fbox .mc_chartdata{display:block;}
    .fly_fbox .mc_chartdata_spec{margin:10px 0 0 0;}
    .mc_chart_count_fxwrap{margin:10px 0 0 0;}
    .mc_chart_else_data{margin:25px 0 0 0;}
}
@media (max-width:1023px){
    .mc_contents_wrap{padding:15px 20px 40px}
    .mv_short{flex-basis: 150px;margin-left:7px;}
    // .btn_mvlk{font-size:12px;padding:10px;}
    // .btn_mvlk.more_type:after{width:12px;height:12px;}
    // .btn_mvlk_vlist>li{margin-bottom:5px;}

    .mv_both{display:block;}
    .mv_long{}
    .mv_short{margin:10px 0 0 0;}

    .mc_cont_vitem{margin-bottom:20px;}
    .mc_cont_vtit{font-size:17px;}
    .mc_cont_vtitlow{margin-bottom:5px;}

    .btn_mvlk{font-size:14px;}
    .mc_fly_both{display:block;}
    .mc_fly_map{margin:10px;}
    .mc_map_api_zone{width:auto;height:400px;}
    .mc_contents_wrap .dotguide_w{margin-top:-5px;}
    .mc_contents_wrap .dotguide_w .dotguide_hlist{width:auto;font-size:0;line-height:0;}
    .mc_contents_wrap .dotguide_w .dotcirle{top:0.5px;}
    .mc_wavechart_box{padding:20px;}
    .hor_fxwrap_wrap{padding:15px 0;flex-wrap:wrap;}
    .hor_fxwrap_wrap .hor_fxitem{width:50%;flex:0 0 50%;padding:10px;box-sizing: border-box;}
    .hor_fxlab{margin-bottom:10px;font-size:13px;}
    .hor_fxnum{font-size:14px;}
    .hor_fxunit{font-size:14px;top:0;}
    .hor_fxdata img{width:19px;}
    .line_chart_tit{font-size:14px;margin-top:15px;padding-bottom:10px;}

    .fly_fbox .mc_chartdata_thum{width:30px;height:30px;}
        
    .mc_chartdata.type2 .mc_chartdata_thum{background-position:0 -30px;background-color:#8373ff;}
    .mc_chartdata.type3 .mc_chartdata_thum{background-position:0 -60px;background-color:#3bcbf5;}
    .mc_chartdata.type4 .mc_chartdata_thum{background-position:0 -90px;background-color:#f86185;}

    .fly_fbox .mc_chartdata_spec{font-size:16px;}
    .mc_chart_count{font-size:17px;}
    .mc_chart_count_else{font-size:13px;padding-bottom:0;}

    .mc_chart_else_data{display:block;}
    .mc_chart_else_lab{display:block;}
    .mc_chart_else_value{display:block;}
    .fly_fbox .data_w{padding:15px !important;}

    .select_calendar{height:32px;font-size:13px;padding: 0 35px 0 35px;line-height:30px;background-size:15px auto;background-position:15px calc(50% - 1px)}
    .select_calendar_w:before{width:7px;height:7px;top:10px;right:15px;}

    .mc_barchart_fxwrap{display:block;}
    .mc_barchart_fxcell{}
    .mc_barchart_box{height:auto;}
    .mc_wbox_tit{font-size:14px;}
    .mc_total_tb td{font-size:14px;}
    .mc_wbox_tit.has_ico:before{top:-3px;}
    .mctextlk_text{font-size:14px;}
    .mctextlk_ico{flex:0 0 14px;width:14px;height:14px;}
    .mc_textlk_zone .nodata_para{padding:50px 20px;}
}